<template>
<div class="ickt-shopping-car">
    <div class="price-num">共 ￥{{$store.getters.price}} 元</div>
    <div class="buy-btn" @touchend="showPaymentPage">选好了</div>
</div>
</template>
<style scoped lang="scss">
.ickt-shopping-car {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    height: 50px;
    line-height: 50px;
    font-size: 20px;
    color: #fff;
    background: #484d54;
    display: flex;
    text-align: center;
    .price-num {
        flex: 1;
    }
    .buy-btn {
        width: 100px;
        background: #fe2947;
    }
}
</style>
<script>
export default {
    // 方法
    methods: {
        // 进入购买页面
        showPaymentPage() {
            // 如果已经有商品加入购物车
            if (this.$store.state.products.length) {
                // 跳转页面
                this.$router.push('/payment')
            } else {
                // 提示用户
                alert('请将商品加入购物车！')
            }
        }
    }
}
</script>